.docs{
  padding: 3.75em 0 7.5em;
  &__grid{
    margin-top: 2.75em;
  }
}

.info-block{
  @include grid100gap(.75em);
  max-width: 64.375em;
}

.medals{
  padding: 7.5em 0 3.75em;
  &__grid{
    margin-top: 2.75em;
  }
}

.medal-card{
  padding: 1.875em .375em;
  background: #453c46;
  border-radius: .875em;
  color: $white;
  &:hover{
    color: $white;
  }
  &__img{
    height: 10.25em;
    display: flex;
    justify-content: center;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 6.25em;
      height: 10.75em;
      filter: blur(5.625em);
      background: #251e26;
      right: 1.75em;
      top: 1.125em;
      transform: rotate(-30deg);
    }
    img{
      max-width: 100%;
      max-height: 100%;
      position: relative;
      z-index: 2;
    }
  }
  &__text{
    @include grid100gap(.25em);
    margin-top: 1.5em;
    text-align: center;
  }
}

.doc-group{
  &._open-modal{
    .doc-modal{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.doc-card{
  cursor: pointer;
  position: relative;
  color: $white;
  &:not(:hover){
    .doc-card__content{
      opacity: 0;
    }
  }
  &__img{
    @include imgCover(115%);
    border-radius: .875em;
  }
  &__content{
    transition: all .3s;
    background: rgba(69, 46, 71, 0.65);
    border-radius: .875em;
    position: absolute;
    top: 0;
    left: 0;
    @include center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: .25em;
  }
  &__btn{
    font-size: 1em;
    line-height: 150%;
    text-decoration: underline;
    margin-top: .5em;
    background-color: transparent;
    color: $white;
    position: relative;
    &:hover{
      color: $light-gray;
    }
    &:before,
    &:after{
      content: attr(data-title);
      @include centerAbsolute;
      width: 100%;
      text-decoration: underline;
    }
    &:before{
      filter: blur(4px);
    }
    &:after{
      filter: blur(8px);
    }
  }
}

.doc-modal{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $vh;
  z-index: 20;
  overflow-y: auto;
  background-color: rgba($primary,.9);
  color: $white;
  display: flex;
  flex-direction: column;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  .container{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__close{
    background-color: transparent;
    position: fixed;
    top: 2.5em;
    right: 2.5em;
    width: 3em;
    @include center;
    &:hover{
      opacity: .8;
    }
    img{
      width: 100%;
      display: block;
    }
  }
  &__inner{
    flex-grow: 1;
    padding: 8em 0;
    display: grid;
    grid-template-columns: 49.25em 34.25em;
    justify-content: space-between;
    gap: 3.75em;
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
}

.hero-text{
  padding-bottom: 7.75em;
  &__top{
    @include grid100gap(.75em);
    max-width: 47.125em;
    margin-bottom: 2.25em;
  }
  &__desc{
    line-height: 150%;
    font-weight: 700;
    opacity: .7;
  }
}


@media screen and (max-width: 992px){
  .docs{
    padding: 1.875em 0 5.625em;
    &__grid{
      margin-top: 2.5em;
    }
  }

  .info-block{
    gap: .5em;
  }

  .medals{
    padding: 3.75em 0 1.875em;
    &__grid{
      margin-top: 2.5em;
    }
  }

  .medal-card{
    &__img{
      height: 9.375em;
    }
  }

  .doc-modal{
    display: block;
    &__inner{
      padding: 10em 0 2.75em;
      grid-template-columns: 100%;
    }
    &__img{
      order: -1;
      max-width: 19.375em;
      margin: 0 auto;
    }
    .text{
      h3{
        font-size: 1.75em;
        line-height: 129%;
        max-width: 90%;
      }
      p{
        font-size: 1.5em;
      }
    }
  }

  .hero-text{
    padding-bottom: 5.625em;
    &__top{
      gap: .5em;
      margin-bottom: 1.5em;
    }
    .h2{
      line-height: 106%;
    }
  }
}


