.footer{
  padding: 1.25em 0 2.125em;
  background-color: $primary;
  color: $white;
  &__top,
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 14em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: .5em;
    padding-top: 1.5em;
    border-top: 1px solid rgba(234, 234, 234, 0.08);
  }
  &__email{
    font-size: 1em;
    line-height: 150%;
    font-weight: 500;
    &:hover{
      color: rgba($white, 0.7);
    }
  }
}

.footer-nav{
  display: flex;
  align-items: center;
  &__link{
    color: rgba($white, 0.7);
    font-size: .875em;
    line-height: 150%;
    margin-right: 3.14em;
    &:last-child{
      margin: 0;
    }
    &:hover{
      color: $white;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__logo{
      width: 12em;
    }
    &__bottom{
      display: block;
      .footer-nav{
        margin-top: 1.25em;
      }
    }
    &__email{
      margin: 0 0 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding-top: 2.125em;
    &__inner{
      position: relative;
    }
    &__top{
      flex-direction: column;
    }
    &__bottom{
      margin-top: 1.5em;
      .footer-nav{
        margin: 0;
      }
    }
    &__logo{
      margin-bottom: 4em;
    }
    &__email{
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 5.25em;
    }
  }

  .footer-nav{
    margin-top: 1.25em;
    flex-direction: column;
    &__link{
      margin: 0 0 1.71em;
    }
  }
}
