@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.75em;
    height: 5em;
    border-radius: 31.25em;
    font-weight: 700;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
    }
}

.m-btn-brown{
    background-color: $beige;
    color: $white;
    position: relative;
    &._no-shadow{
        &:before{
            display: none;
        }
    }
    &:before{
        content: '';
        @include centerAbsolute;
        background-image: url(../img/btn-ellipse.svg);
        width: 12.5em;
        height: 2.6875em;
        @include bgImgSize(100%);
        filter: blur(40px);
        pointer-events: none;
        transition: all .3s;
    }
    span{
        position: relative;
        z-index: 2;
    }
    &:hover{
        &:before{
            filter: blur(28px);
        }
    }
    &:active{
        background-color: rgba($beige,.7);
        &:before{
            filter: blur(40px);
        }
    }
    &:disabled{
        background-color: rgba($beige,.25);
        color: rgba($white,.4);
        &:before{
            opacity: 0;
        }
    }
}

.m-btn-white-outline{
    font-weight: 400;
    border: 1px solid $white;
    background: rgba(243, 239, 236, 0.14);
    color: $white;
    &:hover{
        background: rgba(243, 239, 236, 0.36);
        color: $white;
    }
    &:active{
        background: rgba(243, 239, 236, 0.14);
        color: $white;
    }
    &:disabled{
        border-color: rgba($white,.25);
        color: rgba($white,.4);
    }
}

.m-btn-purple{
    background-color: #412c43;
    color: $white;
    &:hover{
        background-color: #5a3e5c;
        color: $white;
    }
    &:active{
        background-color: #412c43;
        color: $white;
    }
    &:disabled{
        background-color: rgba(65, 44, 67, 0.25);
        color: rgba($white,.4);
    }
}

.m-btn-text-white{
    color: $white;
    &:hover{
        color: $beige;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 4em;
        padding: 0 2.125em;
        span{
            font-size: 1em;
        }
    }
}


.m-btn-medium{
    padding: 0 1.375em;
    height: 3em;
    span{
        font-size: .875em;
    }
}
