.what{
  padding: 8.375em 0;
  &__top{
    width: 63.5em;
    max-width: 100%;
    margin: 0 auto;
    @include grid100gap(1.625em);
  }
  &__desc{
    line-height: 183%;
    opacity: .75;
  }
  &__inner{
    padding: 7.25em;
    background-color: $primary;
    color: $white;
    border-radius: 1.25em;
    margin-top: 6.25em;
    display: grid;
    grid-template-columns: 21.25em 19.625em;
    justify-content: space-between;
    gap: 2.375em;
    align-items: center;
    overflow: hidden;
  }
  &__img{
    position: relative;
    img{
      width: 100%;
      display: block;
      border-radius: 1.25em;
    }
    @media screen and (min-width: 993px){
      display: none;
    }
  }
  &__title{
    font-size: 2.375em;
    line-height: 110%;
    font-weight: 700;
  }
  &__subtitle{
    font-size: 1.5em;
    line-height: 133%;
    font-weight: 700;
    margin-top: .5em;
  }
  &__nav{
    display: flex;
    margin-top: 2.25em;
  }
}

.what-line{
  display: none;
  &:hover{
    .what-line__items{
      animation-play-state: paused;
    }
  }
  @media screen and (min-width: 993px){
    display: block;
    height: 24.25em;
    transform: translateY(-110%);
    .what__img{
      display: block;
      margin-bottom: 2.5em;
    }
    &__items{
      animation: 20s slides infinite linear;
    }
  }
}

@keyframes slides {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-100%);
  }
}

.small-container{
  width: 63.75em;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.features{
  padding: 9.125em 0;
  &__groups{
    @include grid100gap(11.25em);
  }
}

.feature-group{
  display: grid;
  grid-template-columns: 30.25em calc(100% - 37.25em);
  gap: 2.375em 7em;
}

.history{
  &__title{
    font-size: 1.25em;
    line-height: 150%;
    font-weight: 700;
  }
  &__text{
    font-weight: 700;
    margin-top: .625em;
    p{
      font-size: .875em;
      line-height: 186%;
      margin-bottom: 1.71em;
      &:last-child{
        margin-bottom: 0;
      }
    }
    img{
      width: 100%;
      display: block;
      border-radius: .25em;
      margin-bottom: 1.5em;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
  &__btn{
    margin-right: .75em;
    &:last-child{
      margin: 0;
    }
  }
  &__nav2{
    display: flex;
    margin-top: 2.75em;
  }
  &__save{
    display: flex;
    align-items: center;
    font-weight: 700;
    color: $black;
    span{
      font-size: 1.125em;
    }
    svg{
      width: 1.75em;
      margin-left: .625em;
    }
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
  }
}

.important-things{
  &__grid{
    column-count: 2;
    column-gap: 1.375em;
  }
  &__add{
    margin-bottom: 1.375em;
    border-radius: 1.25em;
    background-color: #412c43;
    padding: 1.875em 1.5em 1.625em;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $white;
    font-weight: 700;
    &:hover{
      color: $white;
      background-color: darken(#412c43,5%);
    }
    img{
      width: 7.25em;
      margin-bottom: 1em;
    }
  }
}

.important-card{
  border-radius: 1.25em;
  display: inline-flex;
  width: 100%;
  margin-bottom: 1.375em;
  color: $white;
  position: relative;
  &:hover{
    color: $white;
    &:after{
      background: rgba(#452E47,.65);
    }
  }
  &:active{
    &:after{
      background: rgba(#452E47,.9);
    }
    .important-card__title{
      &:after{
        opacity: 1;
      }
    }
  }
  &:after{
    transition: all .3s;
    background: rgba(#2F272C,.2);
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: 1.25em;
  }
  &__content{
    @include centerAbsolute;
    @include center;
    width: 100%;
    z-index: 2;
  }
  &__title{
    position: relative;
    &:after{
      content: attr(data-title);
      @include centerAbsolute;
      width: 100%;
      //filter: blur(4px);
      transition: all .3s;
      opacity: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .what{
    padding: 4.375em 0 2.25em;
    &__top{
      gap: .625em;
    }
    &__inner{
      grid-template-columns: 100%;
      padding: 3.625em 3em;
      margin-top: 2.5em;
    }
    &__img{
      order: -1;
      width: 17em;
      max-width: 100%;
    }
  }

  .features{
    padding: 2.25em 0;
    &__groups{
      gap: 4em;
    }
  }

  .feature-group{
    grid-template-columns: 100%;
  }

  .history{
    &__text{
      font-weight: 400;
    }
  }

  .important-things{
    &__grid{
      @media screen and (min-width: 577px){
        column-count: 3;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .what{
    &__title{
      font-size: 1.75em;
    }
    &__subtitle{
      font-size: 1.25em;
      line-height: 160%;
    }
  }
}
