.particles-js{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.main{
  background-color: $primary;
  padding: 14.375em 0 0;
  color: $white;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &__text{
    text-align: center;
    @include grid100gap(1.625em);
  }
  &__subtitle{
    letter-spacing: 0.24em;
    opacity: .6;
  }
  &__desc{
    letter-spacing: 0.04em;
  }
  &__nav{
    margin-top: 4.625em;
    display: flex;
    justify-content: center;
  }
  &__bottom{
    margin-top: 8.625em;
    padding: 0 .625em;
  }
  &__tree{
    width: 60em;
    margin: 0 auto;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
}

.offer{
  padding-bottom: 3.125em;
  color: $white;
  &__grid{
    position: relative;
    z-index: 2;
    margin-top: -4.6875em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
    align-items: flex-start;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
    gap: .625em;
    background-color: $white;
    border-radius: 1.25em;
    &._locked{
      pointer-events: none;
      .offer__title{
        background-image: url(../img/lock.svg);
        background-repeat: no-repeat;
        background-position: left 2em top 3em;
        background-size: 1.5em auto;
        &-text{
          opacity: .5;
        }
      }
      .offer-card{
        opacity: .5;
      }
    }
  }
  &__title{
    background-color: rgba(78, 56, 80, 0.94);
    height: 26em;
    @include center;
    border-radius: 1.25em;
    grid-column: 1/-1;
    &-text{
      font-size: 4em;
      font-weight: 700;
    }
  }
}

.offer-card{
  min-height: 10.25em;
  position: relative;
  border-radius: 1.25em;
  font-weight: 700;
  display: block;
  overflow: hidden;
  &:hover{
    color: $white;
    .offer-card__img{
      //transform: scale(1.05);
    }
  }
  &__backdrop,
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: inherit;
  }
  &__img{
    transition: all .6s;
  }
  &__backdrop{
    z-index: 2;
  }
  &__content{
    position: relative;
    z-index: 3;
    padding: 1.25em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    min-height: 100%;
  }
  &__title{
    margin-bottom: .5em;
  }
  &__btn{
    margin: 0 0 auto auto;
    width: 1.875em;
    transform: rotate(-45deg);
    img{
      width: 100%;
      display: block;
    }
    span{
      display: none;
    }
  }

  @media screen and (min-width: 993px){
    &._big{
      grid-column: span 2;
      .offer-card__desc{
        font-size: 1.5em;
      }
    }
    &._big2{
      grid-column: span 2;
      grid-row: span 2;
      .offer-card__content{
        padding: 3.125em 2em 2em;
        justify-content: unset;
      }
      .offer-card__desc{
        font-size: 1.75em;
      }
      .offer-card__btn{
        margin: auto 0 0;
        display: flex;
        align-items: center;
        font-weight: 700;
        order: 3;
        transform: unset;
        img{
          width: 1.75em;
          margin-left: .625em;
        }
        span{
          display: block;
          font-size: 1.125em;
        }
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding-top: 12em;
    &__text{
      gap: .625em;
      padding: 0 3.75em;
    }
    &__nav{
      margin-top: 2.375em;
    }
    &__desc{
      line-height: 171%;
    }
    &__bottom{
      margin-top: 8em;
    }
  }

  .offer{
    padding-bottom: .25em;
    &__grid{
      margin-top: -3em;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
    }
    &__title{
      height: 15em;
      &-text{
        font-size: 2.375em;
      }
    }
    &__items{
      &._locked .offer__title{
        background-position: left 1.75em top 1.75em;
      }
    }
  }

  .offer-card{
    min-height: 10em;
    grid-column: span 2;
    &__btn{
      position: absolute;
      top: 1.25em;
      right: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__subtitle{
      margin-bottom: .5em;
    }
  }

  .offer{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
