.header{
  background-color: $primary;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 15.5em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-index{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  padding: 3em 0;
}

.burger{
  display: none;
}

.menu{
  margin-left: auto;
  &__top{
    display: none;
  }
  &__bottom{
    display: flex;
    align-items: center;
  }
}

.header-action{
  margin-left: 3.375em;
}

.nav{
  display: flex;
  &__link{
    color: rgba($white,.7);
    margin-right: 2.75em;
    &:last-child{
      margin: 0;
    }
    &:hover{
      color: $white;
    }
    img{
      display: none;
    }
    span{
      font-size: .875em;
      line-height: 171%;
    }
  }
}

.avatar{
  width: 2.25em;
  height: 2.25em;
  border-radius: 50%;
  color: $white;
  @include center;
  img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius: inherit;
    object-fit: cover;
  }
  &__text{
    font-size: .875em;
    font-weight: 700;
  }
}

.header-user{
  display: flex;
  align-items: center;
  margin-left: 5.75em;
  color: $white;
  position: relative;
  &._open{
    .header-user__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__info{
    display: flex;
    align-items: center;
    &:hover{
      color: $white;
    }
  }
  .avatar{
    margin-right: .625em;
    background-color: #3C4446;
  }
  &__btn{
    width: 1.125em;
    @include center;
    margin-left: .375em;
    img{
      width: 100%;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    display: flex;
    align-items: center;
    color: rgba($black,.7);
    white-space: nowrap;
    margin-bottom: 1em;
    &:last-child{
      margin-bottom: 0;
    }
    img{
      width: 1.5em;
      margin-right: 1.125em;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
  &__dropdown{
    @media screen and (min-width: 993px){
      position: absolute;
      right: 0;
      top: 100%;
      margin-top: 1.25em;
      padding: 1.5em 1.25em;
      background-color: $white;
      border-radius: .875em;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
    }
  }
  &__bottom{
    margin-top: 1em;
    padding-top: 1em;
    border-top: 1px solid #EAEAEA;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.125em 0;
    &__logo{
      width: 9.5em;
    }
    &__btn{
      padding: 0 1em;
      height: 2.375em;
      margin-left: .5em;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    z-index: 20;
    background-color: $white;
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 5.6875em;
      background-color: $primary;
      width: 100%;
      padding: 0 2.125em;
      border-radius: 0 0 .875em .875em;
    }
    &__link{
      display: flex;
      align-items: center;
      color: $white;
      .avatar{
        margin-right: .5em;
        &._bg{
          background-color: #AD8D81;
        }
      }
    }
    &__bottom{
      height: calc(100% - 5.6875em);
      overflow-y: auto;
      padding: 2.5em 2.125em;
      flex-direction: column;
      align-items: unset;
    }
  }

  .burger{
    width: 2.25em;
    height: 2.25em;
    @include center;
    margin-left: auto;
    position: relative;
    z-index: 21;
    img{
      width: 100%;
      @include centerAbsolute;
    }
    ._view2{
      opacity: 0;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      color: rgba($black,.7);
      margin: 0 0 1.875em;
      display: flex;
      align-items: center;
      &:hover{
        color: $black;
      }
      span{
        font-size: 1em;
        line-height: 150%;
      }
      img{
        display: block;
        width: 1.5em;
        margin-right: 1.125em;
      }
    }
  }

  .header-user{
    margin: 1.75em 0 0;
    padding-top: 1.75em;
    flex-grow: 1;
    flex-direction: column;
    align-items: unset;
    border-top: 1px solid #EAEAEA;
    width: 100%;
    &__dropdown{
      display: flex;
      flex-direction: column;
      flex-grow: 1;
    }
    &__links{
      flex-grow: 1;
    }
    &__info,
    &__btn{
      display: none;
    }
    &__link{
      margin-bottom: 2em;
    }
    &__bottom{
      margin-top: 0;
      border-top: none;
    }
  }
}


