.guide-slider{
  overflow: hidden;
  position: relative;
  z-index: 2;
  .swiper{
    &-container{
      height: 44.25em;
    }
    &-slide{

    }
    @media screen and (min-width: 993px){
      &-pagination{
        left: 50%;
        bottom: 50%;
        transform: translate(-50%,50%) !important;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: auto;
        //height: 100%;
        top: unset;
        &-bullet{
          flex-shrink: 0;
          margin: 0 0 4.375em !important;
          width: .875em;
          height: .875em;
          opacity: 1;
          background-color: transparent;
          border: 1px solid $white;
          position: relative;
          &:before{
            content: '';
            position: absolute;
            top: 100%;
            left: 50%;
            transform: translateX(-50%);
            height: 4.375em;
            width: .1875em;
            background-color: $white;
          }
          &:last-child{
            margin-bottom: 0 !important;
            &:before{
              display: none;
            }
          }
          &:first-child,
          &:last-child{
            width: 1.25em;
            height: 1.25em;
          }
          &-active{
            background-color: $white;
          }
        }
      }
    }
  }
}

.guide-card{
  padding: 10em 0;
  height: 44.25em;
  &__grid{
    display: grid;
    grid-template-columns: 40.75em calc(100% - 54.25em);
    justify-content: space-between;
    align-items: center;
    width: 83em;
    margin: 0 auto;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__col2{
    max-width: 22em;
  }
  &__title{
    max-width: 7em;
    font-size: 2.25em;
    line-height: 156%;
    font-weight: 700;
  }
  &__desc{
    font-size: 1.5em;
    margin-top: 1.33em;
    line-height: 158%;
  }
  &__nav{
    display: flex;
    margin-top: 5em;
  }
}


@media screen and (max-width: 992px){
  .guide-slider{
    .swiper {
      &-container {
        height: unset;
      }
      &-pagination{
        bottom: 6.375em;
        &-bullet{
          width: .5em;
          height: .5em;
          background-color: $white;
          opacity: 1;
          margin: 0 .3125em !important;
          position: relative;
          &:after,
          &:before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            background-color: $white;
            z-index: -1;
          }
          &-active{
            //&:before{
            //  filter: blur(8px);
            //}
            //&:after{
            //  filter: blur(4px);
            //}
          }
        }
      }
    }
  }

  .guide-card{
    padding: 3.125em 0 9em;
    overflow: hidden;
    width: 100%;
    text-align: center;
    height: unset;
    &__grid{
      width: 100%;
      grid-template-columns: 100%;
      gap: 3.25em;
    }
    &__col{
      display: flex;
      justify-content: center;
    }
    &__col2{
      max-width: unset;
    }
    &__img{
      height: 20em;
      width: auto;
    }
    &__title{
      font-size: 1.75em;
      line-height: 150%;
      max-width: unset;
    }
    &__desc{
      margin-top: .33em;
    }
    &__nav{
      justify-content: center;
      margin-top: 3.25em;
    }
  }
}


