.hero-main{
  padding-bottom: 7.75em;
  &__grid{
    display: grid;
    grid-template-columns: 36.5em calc(100% - 46em);
    gap: 9.5em;
  }
  &__img{
    border-radius: .875em;
    img{
      width: 100%;
      display: block;
      border-radius: inherit;
    }
  }
  &__col2{
    padding-top: 3.125em;
  }
  &__desc{
    line-height: 150%;
    opacity: .7;
  }
  .props{
    margin-top: 2.5em;
  }
}

.props{
  @include grid100gap(2.5em);
}

.prop-item{
  display: flex;
  &:after{
    content: '';
    background-image: url(../img/prop-line.svg);
    background-size: auto 100%;
    min-width: 1em;
    height: .125em;
    flex-grow: 1;
    margin: 0 .375em .625em;
    align-self: flex-end;
  }
  &__title,
  &__desc{
    font-size: 1.125em;
    line-height: 150%;
    font-weight: 700;
  }
  &__desc{
    order: 2;
  }
}

.hero-detail{
  background-color: $primary;
  padding: 10em 0 11.25em;
  color: $white;
  .text{
    max-width: 65.375em;
  }
}


@media screen and (max-width: 992px){
  .hero-main{
    padding-bottom: 3.75em;
    &__grid{
      grid-template-columns: 20em calc(100% - 20em - 1.5em);
      gap: 1.5em;
    }
    &__col2{
      padding: 1em 0 0;
    }
    &__img{
      @include imgCover(127.7%);
    }
    &__title{
      line-height: 106%;
    }
    &__desc{
      margin-top: .5em;
    }
    .props{
      margin-top: 2.25em;
    }
  }

  .props{
    gap: 1.5em;
  }

  .prop-item{
    &__title,
    &__desc{
      font-size: .875em;
    }
    &__title{
      font-weight: 400;
      opacity: .7;
    }
  }

  .hero-detail{
    padding: 2.5em 0;
  }
}


@media screen and (max-width: 576px){
  .hero-main{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col2{
      padding: 0;
    }
  }
}
