.technology{
  padding: 11.25em 0 15.5em;
  &__inner{
    display: grid;
    grid-template-columns: 30.3125em calc(100% - 36.3125em);
    gap: 1.5em 6em;
  }
  &__items{
    display: grid;
    grid-template-rows: repeat(var(--cards-count), var(--card-height));
    gap: 1em 0;
  }
  .h3{
    position: sticky;
    top: 1em;
  }
  &__bottom{
    margin-top: 12.5em;
  }
}

.technology-card{
  position: sticky;
  top: 1em;
  &__inner{
    border: 1px solid rgba(242, 241, 244, 0.3);
    background-image: url(../img/technology-bg.svg);
    background-size: cover;
    background-position: center;
    border-radius: 1.5em;
    min-height: 18.75em;
    padding: 1.25em 3.125em;
    color: $white;
    display: flex;
    align-items: center;
    position: relative;
  }
  &__title{
    font-size: 1.25em;
    line-height: 160%;
    letter-spacing: 0.04em;
    position: relative;
    z-index: 2;
  }
}

.video{
  display: block;
  position: relative;
  .play-btn{
    @include centerAbsolute;
  }
  &__img{
    border-radius: 2em;
    img{
      width: 100%;
      border-radius: inherit;
      display: block;
    }
  }
}

.play-btn{
  width: 4em;
  height: 4em;
  @include center;
  border-radius: 50%;
  background-color: $white;
  position: relative;
  &:hover{
    path{
      fill: $beige;
    }
  }
  &:active{
    path{
      fill: #101521;
    }
  }
  &:after,
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: $white;
    z-index: -1;
  }
  //&:before{
  //  filter: blur(2.4150943756103516px);
  //}
  //&:after{
  //  filter: blur(4.830188751220703px);
  //}
  svg{
    width: 1.6875em;
    transform: translateX(10%);
  }
}


@media screen and (max-width: 992px){
  .technology{
    padding: 4.625em 0 4.375em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__items{
      grid-template-rows: unset;
    }
    .h3{
      position: static;
    }
    &__bottom{
      margin-top: 4.375em;
    }
  }

  .technology-card{
    position: static;
    padding: 0 !important;
    top: unset !important;
    &__inner{
      padding: 1.25em 2.5em;
    }
  }

  .video{
    &__img{
      border-radius: .8125em;
    }
  }
}


