.breadcrumbs {
    padding: 1.5em 0 4.875em;
    &:last-child{
        margin-bottom: 0;
    }
    li{
        color: rgba($black,.7);
        position: relative;
        display: inline;
        &:not(:first-child) {
            margin-left: 1.25em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                top: .625em;
                right: 100%;
                margin-right: .5em;
                width: .25em;
                height: .25em;
                border-radius: 50%;
                background-color: $primary;
            }
        }
        &:last-child{
            pointer-events: none;
            color: $primary;
        }
    }
    a{
        font-size: .875em;
        line-height: 170%;
    }
}

.breadcrumbs-mob{
    display: none;
}



@media screen and (max-width: 992px){
    .breadcrumbs{
        display: none;
    }

    .breadcrumbs-mob{
        display: flex;
        padding: 1.5em 0 2.5em;
        &__back{
            color: rgba($black,.7);
            font-weight: 700;
            display: flex;
            align-items: center;
            img{
                width: 1.5em;
                margin-right: .25em;
            }
            span{
                font-size: 1em;
                line-height: 150%;
            }
        }
    }
}
