.unique{
  position: relative;
  padding-top: 8.3125em;
  background-color: $primary;
  color: $white;
  .container{
    position: relative;
    z-index: 2;
  }
  &__text{
    @include grid100gap(1.625em);
    width: 63em;
    max-width: 100%;
    text-align: center;
    margin: 0 auto;
  }
  &__subtitle,
  &__desc{
    opacity: .75;
  }
  &__desc{
    line-height: 183%;
  }
  &__media{
    width: 52.5em;
    max-width: 100%;
    margin: 12em auto 0;
    padding-right: 2.125em;
    position: relative;
  }
  &__tree{
    position: relative;
    img{
      width: 100%;
      display: block;
    }
    &-img1{
      //mix-blend-mode: screen;
    }
    &-img2{
      position: absolute;
      top: 0;
      left: 0;
    //  filter:
    //          drop-shadow(2.125em .5625em 4.8125em rgba(0, 0, 0, 0.18))
    //  drop-shadow(8.375em 2.25em 8.6875em rgba(0, 0, 0, 0.15))
    //  drop-shadow(18.75em 5em 11.875em rgba(0, 0, 0, 0.09))
    //  drop-shadow(33.75em 9em 14em rgba(0, 0, 0, 0.03))
    //;

    }
  }
}

.unique-card{
  transition: all .3s;
  position: absolute;
  right: -1.375em;
  bottom: 8.75em;
  width: 20.875em;
  img{
    transform: rotate(11deg);
    width: 100%;
    display: block;
    //filter:
    //        drop-shadow(1.75em 1.5625em 5em rgba(33, 18, 33, 0.29))
    //        drop-shadow(7em 6.4em 9.5em rgba(33, 18, 33, 0.26))
    //        drop-shadow(16em 14.5em 13em rgba(33, 18, 33, 0.15))
    //        drop-shadow(28em 25em 15em rgba(33, 18, 33, 0.04))
    //        drop-shadow(44em 39em 16em rgba(33, 18, 33, 0.01));

  }
  &._type2{
    bottom: 8.25em;
    right: 3.5em;
    img{
      filter: unset;
    }
  }
}

.unique-steps{
  margin: 8.5em auto 0;
  width: 73.5em;
  max-width: 100%;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
  }
  &__text{
    margin-top: 2.2em;
    padding: 0 4.8em;
    text-align: center;
    font-size: 1.25em;
    line-height: 180%;
    font-weight: 700;
  }
}

.step-card{
  padding: 1em 1.5em 1.625em;
  border-radius: 1.25em;
  background-color: $white;
  color: #1e1e1e;
  &__num{
    font-size: 5em;
    line-height: 137%;
    letter-spacing: 0.05em;
    color: rgba($primary,.1);
    font-family: $font2;
  }
  &__title{
    font-size: 1em;
    line-height: 150%;
    letter-spacing: 0.05em;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .unique{
    padding-top: 4.375em;
    &__text{
      gap: .625em;
    }
    &__media{
      margin-top: 4.5em;
      font-size: .75em;
    }
  }

  .unique-steps{
    margin-top: 6.25em;
    &__grid{
      grid-template-columns: repeat(4,calc(25% - 1.125em / 4));
      gap: .375em;
    }
    &__text{
      font-size: 1em;
      padding: 0 1em;
      margin-top: 1.71em;
      line-height: 200%;
    }
  }

  .step-card{
    padding: 1.25em 1em 1em;
    font-size: .875em;
    &__num{
      font-size: 3.5em;
      line-height: 116%;
    }
    &__title{
      font-size: .875em;
      line-height: 171%;
      margin-top: auto;
      padding-top: 1.28em;
    }
  }
}


@media screen and (max-width: 576px){
  .unique{
    &__subtitle{
      margin-bottom: .5em;
    }
    .h1{
      font-size: 2.25em;
    }
    &__media{
      font-size: 1em;
      padding: 0 1.25em 0 .75em;
    }
  }

  .unique-card{
    width: 10.25em;
    right: 2em;
    bottom: 4.5em;
    &._type2{
      bottom: 4.25em;
      right: 4.75em;
    }
  }

  .unique-steps{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .1875em));
    }
    &__text{
      font-size: .875em;
    }
  }

  .step-card{
    font-size: 1em;
    display: flex;
    flex-direction: column;
  }
}
